        transform: translate(0, 4px) rotate(0)
    }

    96% {
        transform: translate(0, 1px) rotate(0)
    }

    98% {
        transform: translate(0, 7px) rotate(0)
    }

    0%,100% {
        transform: translate(0, 0) rotate(0)
    }
}
</style>



